.listItem {
    width: 225px;
    height: 120px;
    background-color: var(--main-color);
    overflow: hidden;
    cursor: pointer;
    margin-right: 5px;
    color: white;

    img {
        height: 100%;
        width: 100%;
        cursor: pointer;
        object-fit: cover;
    }

    video {
        width: 100%;
        height: 140px;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
    }

    &:hover {
        width: 325px;
        height: 300px;
        -webkit-box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
        box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
        position: absolute;
        top: -150px;
        border-radius: 5px;

        img {
            height: 140px;
        }
    }

    .itemInfo {
        display: flex;
        flex-direction: column;
        padding: 5px;

        .icons {
            display: flex;
            margin-bottom: 10px;

            .icon {
                border: 2px solid white;
                padding: 5px;
                border-radius: 50%;
                margin-right: 10px;
                font-size: 16px;
            }
        }

        .itemInfoTop{
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            font-size: 14px;
            font-weight: 600;
            color: gray;

            .limit {
                border: 1px solid gray;
                padding: 1px 3px;
                margin: 0 10px;
            }
        }

        .desc {
            font-size: 13px;
            margin-bottom: 10px;

        }
        
        .genre {
            font-size: 14px;
            color: lightgray;
        }
        
    }

}